import {ExcalidrawElement} from "../../element/types";
import {AppState} from "../../types";
import {
  clearAppStateForLocalStorage,
  getDefaultAppState,
} from "../../appState";
import {clearElementsForLocalStorage} from "../../element";
import {STORAGE_KEYS} from "../app_constants";
import {ImportedDataState} from "../../data/types";
import $ from 'jquery'

function getQueryStringByUrl(url: string, name: string) {
  let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
  let r = url.substring(url.indexOf('?') + 1).match(reg); //获取url中"?"符后的字符串并正则匹配
  let context = "";
  if (r != null)
    context = r[2];
  return context == null || context === "" || context === "undefined" ? "" : decodeURI(context);
}

//直接读取浏览器url
export const getQueryString = (name: string) => {
  return getQueryStringByUrl(location.href, name);
}

export const saveUsernameToLocalStorage = (username: string) => {
  try {
    localStorage.setItem(
      STORAGE_KEYS.LOCAL_STORAGE_COLLAB,
      JSON.stringify({username}),
    );
  } catch (error: any) {
    // Unable to access window.localStorage
    console.error(error);
  }
};
let url_host = "https://web_history.tianyunperfect.cn";
// let url_host = "http://127.0.0.1:9099";
let key_a = getQueryString("a")
let key = STORAGE_KEYS.LOCAL_STORAGE_ELEMENTS + key_a;
let key_state = STORAGE_KEYS.LOCAL_STORAGE_APP_STATE + key_a;

function updateData() {
  let json_str = localStorage.getItem(key);
  let json_str2 = localStorage.getItem(key_state);
  // 同步数据
  setInterval(() => {
    let json_new = localStorage.getItem(key);
    let json_new2 = localStorage.getItem(key_state);
    if (json_str != json_new || json_new2 != json_str2) {
      json_str = json_new;
      json_str2 = json_new2;
      $.ajax({
        type: "POST",
        dataType: "json",
        async: false,
        contentType: "application/json;charset=utf-8",
        url: `${url_host}/excalidraw/update`,
        data: JSON.stringify({
          "id": getQueryString("a"),
          "json_str": json_new,
          "app_state": json_new2
        })
      });
    }
  }, 1000);
}

export const importUsernameFromLocalStorage = (): string | null => {
  try {
    const data = localStorage.getItem(STORAGE_KEYS.LOCAL_STORAGE_COLLAB);
    if (data) {
      return JSON.parse(data).username;
    }
  } catch (error: any) {
    // Unable to access localStorage
    console.error(error);
  }

  return null;
};

export const importFromLocalStorage = () => {
  let id = getQueryString("a");
  if (!id) {
    let dateStr = new Date().getTime() - new Date("2022-07-03").getTime();
    location.href = location.protocol + "//" + location.host + "?a=" + dateStr;
  } else {
    $.ajax({
      type: "POST",
      dataType: "json",
      async: false,
      contentType: "application/json;charset=utf-8",
      url: `${url_host}/excalidraw/get`,
      data: JSON.stringify({
        "id": id
      }),
      success: function (data) {
        if (data.message) {
          localStorage.setItem(key, data.message);
          localStorage.setItem(key_state, data.app_state);
        } else {
          $.ajax({
            type: "POST",
            dataType: "json",
            async: false,
            contentType: "application/json;charset=utf-8",
            url: `${url_host}/excalidraw/insert`,
            data: JSON.stringify({
              "id": id,
              "json_str": "[]",
              "app_state": localStorage.getItem(key_state)
            }),
            success: function (data) {
              localStorage.setItem(key, "[]");
            },
          });
        }
      },
    });
  }


  let savedElements = null;
  let savedState = null;

  try {
    savedElements = localStorage.getItem(key);
    savedState = localStorage.getItem(key_state);
    console.log("初始化加载");
  } catch (error: any) {
    // Unable to access localStorage
    console.error(error);
  }

  let elements: ExcalidrawElement[] = [];
  if (savedElements) {
    try {
      elements = clearElementsForLocalStorage(JSON.parse(savedElements));
    } catch (error: any) {
      console.error(error);
      // Do nothing because elements array is already empty
    }
  }

  let appState = null;
  if (savedState) {
    try {
      appState = {
        ...getDefaultAppState(),
        ...clearAppStateForLocalStorage(
          JSON.parse(savedState) as Partial<AppState>,
        ),
      };
    } catch (error: any) {
      console.error(error);
      // Do nothing because appState is already null
    }
  }
  updateData();
  return {elements, appState};

};

export const getElementsStorageSize = () => {
  try {
    const elements = localStorage.getItem(key);
    const elementsSize = elements?.length || 0;
    return elementsSize;
  } catch (error: any) {
    console.error(error);
    return 0;
  }
};

export const getTotalStorageSize = () => {
  try {
    const appState = localStorage.getItem(key_state);
    const collab = localStorage.getItem(STORAGE_KEYS.LOCAL_STORAGE_COLLAB);
    const library = localStorage.getItem(STORAGE_KEYS.LOCAL_STORAGE_LIBRARY);

    const appStateSize = appState?.length || 0;
    const collabSize = collab?.length || 0;
    const librarySize = library?.length || 0;

    return appStateSize + collabSize + librarySize + getElementsStorageSize();
  } catch (error: any) {
    console.error(error);
    return 0;
  }
};

export const getLibraryItemsFromStorage = () => {
  try {
    const libraryItems: ImportedDataState["libraryItems"] = JSON.parse(
      localStorage.getItem(STORAGE_KEYS.LOCAL_STORAGE_LIBRARY) as string,
    );

    return libraryItems || [];
  } catch (error) {
    console.error(error);
    return [];
  }
};
